<template>
  <div>
    <FullBoxVue class="shadowHover">
      <h1>Cargar plantilla de generación masiva de solicitudes</h1>
      <a @click="descargarExcel" download><p>Descargar plantilla de solicitudes masivas</p></a>
    </FullBoxVue>
    <MiddleBox class="shadowHover">
      <h2>Generador de Servicios y subservicios para plantilla</h2>

      <searchUserVue
        v-bind:people="userSearch"
        @selected="userSearchSelectedFunc"
      ></searchUserVue>
      <div v-for="(data, index) in schemaServicio" :key="index">
        <v-select
          @change="buscarSubservicio(valuesServicio[index].servicio, index)"
          v-model="valuesServicio[index].servicio"
          :items="data.servicio"
          item-text="nombre"
          item-value="nombre"
          label="Servicio"
        ></v-select>
        <v-select
          v-model="valuesServicio[index].subservicio"
          :items="data.subservicio"
          item-text="nombre"
          item-value="nombre"
          label="SubServicio"
        ></v-select>
      </div>
      <BlitzForm
        :schema="schemaUbicacion"
        v-model="valuesUbicacion"
        :key="componentKeyUbicacion"
        gridGap="0"
        labelPosition="left"
      />
      {{ msgFormato }}
      <br />
      <v-btn @click="validarGenerador()"> Copiar formato </v-btn>
    </MiddleBox>
    <MiddleBox class="shadowHover">
      <h2>Subir plantilla</h2>
      <div v-if="!loading">
        <br />
        <input type="file" @change="uploadFile" ref="file" />
        <br /><br />
        <v-btn color="primary" dark @click="enviarFormulario">
          Enviar Plantilla
        </v-btn>
      </div>
      <div v-else>
        <loadingCSS>
          Se están creando las solicitudes... <br>
          Esto puede tomar unos momentos
        </loadingCSS>
      </div>
    </MiddleBox>
  </div>
</template>

<script>
import FullBoxVue from "../components/static/FullBox.vue";
import { BlitzForm } from "blitzar";
import Vue from "vue";

import {
  VTextField,
  VRadioGroup,
  VRadio,
  VSlider,
  VCheckbox,
  VSwitch,
  VSelect,
} from "vuetify/lib";
// All components that are used in the form need to be globally registered.
Vue.component("VTextField", VTextField);
Vue.component("VRadioGroup", VRadioGroup);
Vue.component("VRadio", VRadio);
Vue.component("VSlider", VSlider);
Vue.component("VCheckbox", VCheckbox);
Vue.component("VSwitch", VSwitch);
Vue.component("VSelect", VSelect);

import {
  getServicio,
  getSubServicioByName,
  getServicioEspecifico,
  getServicioEspecificoEspecifico,
  getDepartamento,
  downloadFile,
  getCiudad,
  getDocumento,
  leerCargaMasivaDeSolicitudes,
  getClientes,
} from "@/api";
import MiddleBox from "@/components/static/MiddleBox.vue";
import searchUserVue from "../components/searchUser.vue";
import loadingCSS from "../components/static/loadingCSS.vue";

const valuesServiciodef = {
  servicio: "",
  subservicio: "",
};

const valuesUbicaciondef = {
  departamento: "",
  ciudad: "",
};

const valuesCandidatodef = {
  nombreCandidato: "",
  tipodocumento: "",
  documento: "",
  numeroCandidato: "",
  correo: "",
  observaciones: "",
};

export default {
  data() {
    return {
      valuesServicio: [],
      CentroCostos: "",
      schemaServicio: [],

      valuesUbicacion: {
        departamento: "",
        ciudad: "",
      },
      msgFormato: "",
      schemaUbicacion: [],
      componentKey: 0,
      componentKeyUbicacion: -1,

      valuesCandidato: {
        nombreCandidato: "",
        tipodocumento: "",
        documento: "",
        numeroCandidato: "",
        correo: "",
        observaciones: "",
      },
      schemaCandidato: [],
      Images: null,
      datosUser: {},
      userSearch: [],
      userSearchSelected: [],
      serviciosDefault: [],
      loading: false,
    };
  },

  methods: {
    descargarExcel() {
      let filepath="../upload/plantillaSolicitudesMasivas.xlsx"
      downloadFile(filepath).then((response) => {
        console.log(response.data);
        let fileName = filepath.split("/")[2];
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", fileName);
        document.body.appendChild(link);
        link.click();
      });
    },
    validarGenerador() {
      let usuarioSeleccionado = this.userSearchSelected;
      let servicio = this.valuesServicio[0].servicio;
      let subservicio = this.valuesServicio[0].subservicio;
      let departamento = this.valuesUbicacion.departamento;
      let ciudad = this.valuesUbicacion.ciudad;

      if (
        usuarioSeleccionado != "" &&
        servicio != "" &&
        subservicio != "" &&
        departamento != "" &&
        ciudad != ""
      ) {
        let str =
          usuarioSeleccionado +
          "\t" +
          servicio +
          "\t" +
          subservicio +
          "\t" +
          departamento +
          "\t" +
          ciudad +
          "\t";
        this.msgFormato =
          "Formato copiado, lo puede pegar directamente en el excel de la plantilla";
        navigator.clipboard.writeText(str);
      } else {
        this.msgFormato = "Faltan datos para llenar";
      }
    },

    userSearchSelectedFunc(values) {
      this.userSearchSelected = values;
    },
    recargarFormulario() {
      this.componentKey += 1;
      this.componentKeyUbicacion -= 1;
    },

    uploadFile() {
      this.Images = this.$refs.file.files[0];
    },

    enviarFormulario() {
      this.loading = true;
      const formData = new FormData();
      formData.append("file", this.Images);
      leerCargaMasivaDeSolicitudes(formData).then(
        function (response) {
          console.log(response.data);
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer);
              toast.addEventListener("mouseleave", this.$swal.resumeTimer);
            },
          });
          Toast.fire({
            icon: "success",
            title: "Solicitud enviada exitosamente",
          });
          //this.valuesServicio = valuesServiciodef;
          //this.valuesUbicacion = valuesUbicaciondef;
          //this.valuesCandidato = valuesCandidatodef;
          this.recargarFormulario();
          this.loading = false;
        }.bind(this)
      );
      console.log(formData);
    },

    agregarServicios() {
      this.schemaServicio.push(
        structuredClone({
          servicio: this.serviciosDefault,
          subservicio: [],
        })
      );
      this.valuesServicio.push(structuredClone(valuesServiciodef));
    },

    consultaServicio(indiceMayor) {
      getServicio().then(
        function (response) {
          this.preServicios = response.data;
          this.serviciosDefault = [];
          for (let i = 0; i < this.preServicios.length; i++) {
            console.log(this.preServicios[i]);
            this.serviciosDefault.push({
              id: this.preServicios[i].ID_SERVICIO,
              nombre: this.preServicios[i].NOMBRE_SERVICIO,
            });
          }

          this.agregarServicios();
          this.recargarFormulario();
        }.bind(this)
      );
    },

    buscarSubservicio(id, indice) {

      getSubServicioByName(id).then(
        function (response) {
          if (response.data.length > 0) {
            let preSubServicios = response.data;
            let subserviciosDefault = [];
            for (let i = 0; i < preSubServicios.length; i++) {
              subserviciosDefault.push({
                id: preSubServicios[i].ID_SUBSERVICIO,
                nombre: preSubServicios[i].NOMBRE_SUBSERVICIO,
              });
            }
            this.schemaServicio[indice].subservicio = subserviciosDefault;
            this.recargarFormulario();
          }
        }.bind(this)
      );
    },

    consultaServicioEspecifico(id) {
      getServicioEspecifico(id).then(
        function (response) {
          const indice = 3;
          if (response.data.length > 0) {
            const itemsOptionsTemplate = {
              value: "",
              text: "Seleccione el Servicio Específico",
              disabled: true,
            };

            const options = [itemsOptionsTemplate];

            for (let i = 0; i < response.data.length; i++) {
              // Solución planteada así por un error de referencia de memoria
              const itemsOptionsTemplate = {
                value: "",
                text: "",
              };
              const subTemplate = itemsOptionsTemplate;
              itemsOptionsTemplate.value =
                response.data[i].ID_SERVICIO_ESPECIFICO;
              subTemplate.text = response.data[i].NOMBRE_SERVICIO_ESPECIFICO;
              options.push(subTemplate);
            }

            if (this.schemaServicio[indice] == null) {
              this.schemaServicio.splice(indice, 0, {
                id: "servicioEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico",
                items: options,
              });
            } else {
              this.schemaServicio[indice] = {
                id: "servicioEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico",
                items: options,
              };

              this.recargarFormulario();
            }
          } else {
            this.schemaServicio.splice(indice, 1);
          }
        }.bind(this)
      );
    },
    consultaServicioEspecificoEspecifico(id) {
      getServicioEspecificoEspecifico(id).then(
        function (response) {
          const indice = 4;
          console.log(response.data);
          if (response.data.length > 0) {
            const itemsOptionsTemplate = {
              value: "",
              text: "Seleccione el Servicio Específico",
              disabled: true,
            };

            const options = [itemsOptionsTemplate];

            for (let i = 0; i < response.data.length; i++) {
              // Solución planteada así por un error de referencia de memoria
              const itemsOptionsTemplate = {
                value: "",
                text: "",
              };
              const subTemplate = itemsOptionsTemplate;
              itemsOptionsTemplate.value =
                response.data[i].ID_SERVICIO_ESPECIFICO2;
              subTemplate.text = response.data[i].NOMBRE_SERVICIO_ESPECIFICO2;
              options.push(subTemplate);
            }

            if (this.schemaServicio[indice] == null) {
              this.schemaServicio.splice(indice, 0, {
                id: "servicioEspecificoEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico",
                items: options,
              });
            } else {
              this.schemaServicio[indice] = {
                id: "servicioEspecificoEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico",
                items: options,
              };

              this.recargarFormulario();
            }
          } else {
            this.schemaServicio.splice(indice, 1);
          }
        }.bind(this)
      );
    },

    consultaCiudad(id) {
      getCiudad(id).then(
        function (response) {
          const indice = 1;
          if (response.data.length > 0) {
            const itemsOptionsTemplate = {
              value: "",
              text: "Seleccione la ciudad",
              disabled: true,
            };

            const options = [itemsOptionsTemplate];

            for (let i = 0; i < response.data.length; i++) {
              // Solución planteada así por un error de referencia de memoria
              const itemsOptionsTemplate = {
                value: "",
                text: "",
              };
              const subTemplate = itemsOptionsTemplate;
              itemsOptionsTemplate.value = response.data[i].ID_CIUDAD;
              subTemplate.text = response.data[i].NOMBRE_CIUDAD;
              options.push(subTemplate);
            }

            if (this.schemaUbicacion[indice] == null) {
              this.schemaUbicacion.splice(indice, 0, {
                id: "ciudad",
                span: true,
                component: "VSelect",
                label: "Ciudad",
                items: options,
              });
            } else {
              this.schemaUbicacion[indice] = {
                id: "ciudad",
                span: true,
                component: "VSelect",
                label: "Ciudad",
                items: options,
              };

              this.recargarFormulario();
            }
          } else {
            this.schemaUbicacion.splice(indice, 1);
          }
        }.bind(this)
      );
    },
  },

  mounted() {
    getClientes().then(
      function (response) {
        this.userSearch = response.data;
      }.bind(this)
    );

    this.consultaServicio(0);

    getDepartamento().then(
      function (response) {
        const indice = 0;
        const totalForms = 5;
        const itemsOptionsTemplate = {
          value: "",
          text: "Seleccione el departamento",
          disabled: true,
        };

        const options = [itemsOptionsTemplate];

        for (let i = 0; i < response.data.length; i++) {
          // Solución planteada así por un error de referencia de memoria
          const itemsOptionsTemplate = {
            value: "",
            text: "",
          };
          const subTemplate = itemsOptionsTemplate;
          itemsOptionsTemplate.value = response.data[i].ID_DEPARTAMENTO;
          subTemplate.text = response.data[i].NOMBRE_DEPARTAMENTO;
          options.push(subTemplate);
        }

        if (this.schemaUbicacion[indice] == null) {
          this.schemaUbicacion.splice(indice, 0, {
            id: "departamento",
            span: true,
            component: "VSelect",
            label: "Departamento",
            items: options,
          });
        } else {
          this.schemaUbicacion[indice] = {
            id: "departamento",
            span: true,
            component: "VSelect",
            label: "Departamento",
            items: options,
          };
        }
        this.recargarFormulario();
      }.bind(this)
    );

    getDocumento().then(
      function (response) {
        console.log(response.data);
        const indice = 0;
        const totalForms = 5;
        const itemsOptionsTemplate = {
          value: "",
          text: "Seleccionar tipo de documento",
          disabled: true,
        };

        const options = [itemsOptionsTemplate];

        for (let i = 0; i < response.data.length; i++) {
          // Solución planteada así por un error de referencia de memoria
          const itemsOptionsTemplate = {
            value: "",
            text: "",
          };
          const subTemplate = itemsOptionsTemplate;
          itemsOptionsTemplate.value = response.data[i].ID_TIPO_DOCUMENTO;
          subTemplate.text = response.data[i].DESC_COMPLE_TIPO_DOCUMENTOcol;
          options.push(subTemplate);
        }

        const opcionesDocumentos = {
          id: "tipodocumento",
          span: true,
          component: "VSelect",
          label: "Tipo de documento",
          items: options,
        };

        this.schemaCandidato = [
          {
            id: "nombreCandidato",
            component: "VTextField",
            label: "Nombre del candidato",
          },
          opcionesDocumentos,
          {
            id: "documento",
            component: "VTextField",
            label: "Número de documento",
          },
          {
            id: "numeroCandidato",
            component: "VTextField",
            label: "Número telefónico",
          },
          {
            id: "correo",
            component: "VTextField",
            label: "Correo",
          },
          {
            id: "observaciones",
            component: "VTextField",
            label: "Observaciones",
          },
        ];

        this.recargarFormulario();
      }.bind(this)
    );
  },

  watch: {
    valuesUbicacion(newValue, oldValue) {
      if (
        newValue.departamento &&
        newValue.departamento != oldValue.departamento
      ) {
        this.valuesUbicacion.ciudad = "";
        this.consultaCiudad(newValue.departamento);
      }
    },
  },

  components: {
    FullBoxVue,
    BlitzForm,
    MiddleBox,
    searchUserVue,
    loadingCSS
  },
};
</script>

<style scoped>
div {
  width: 100%;
}
</style>
