<template>
  <div :key="componentKey" v-if="valido">
    <FullBoxVue class="shadowHover">
      <h1>Solicitud {{ $route.params.id }}</h1>
    </FullBoxVue>
    <FullBoxVue
      class="shadowHover"
      v-if="
        $dataUser[0].rol == 'Admin' ||
        infoSolicitud.coordinador == $dataUser[0].id
      "
    >
      Encargado(s) actual: {{ infoSolicitud.encargados }}
      <h2>Seleccionar analista/proveedor</h2>
      <searchUserMultipleVue
        v-bind:people="userSearch"
        @selected="userSearchSelectedFunc"
      ></searchUserMultipleVue>
      <v-btn color="success" @click="actualizarEncargado()"
        >Actualizar analista/proveedor</v-btn
      >
      <br /><br />
      <h2>Cambiar estado solicitud</h2>
      <v-select
        v-model="estadoSolicitud"
        :items="estados"
        item-text="nombre"
        item-value="id"
        label="Estado"
      ></v-select>
      <v-btn color="success" @click="actualizarEstadoSolicitud()"
        >Actualizar Estado de solicitud</v-btn
      >
    </FullBoxVue>
    <FullBoxVue class="shadowHover">
      <div>
        <v-textarea
          v-model="observacionesAnalistaProveedor"
          label="Observaciones"
        ></v-textarea>

        <v-btn
          color="success"
          @click="actualizarObservacionesAnalistaProveedor()"
          >Agregar observación</v-btn
        >
        <br /><br /><br />
      </div>
    </FullBoxVue>
    <FullBoxVue
      class="shadowHover"
      v-if="
        $dataUser[0].idRol == 3 ||
        infoSolicitud.idEncargado == $dataUser[0].id ||
        infoSolicitud.encargado == $dataUser[0].id ||
        infoSolicitud.estado == 'Finalizada'
      "
    >
      <div
        v-if="
          $dataUser[0].idRol == 3 || infoSolicitud.encargado == $dataUser[0].id
        "
      >
        <h2>Subir archivo nuevo</h2>
        <input type="file" @change="uploadFile" ref="file" />
        &nbsp;
        <v-btn color="success" @click="subirArchivo()">Subir archivo</v-btn>
      </div>
      <br /><br />

      <v-container>
        <h2>Archivos subidos por colaboradores</h2>
        Pulse encima del archivo para descargar
        <br /><br />
        <v-row dense>
          <v-col
            v-for="(archivo, index) in archivosSubidos"
            :key="index"
          >
            <v-card class="archivo shadowHover">
              <v-btn
                v-if="
                  archivo.idSubidoPor == $dataUser[0].id ||
                  $dataUser[0].idRol == 3
                "
                color="pink"
                fab
                dark
                small
                absolute
                top
                left
                @click="eliminarArchivo(archivo.id)"
              >
                <v-icon>mdi-close</v-icon>
              </v-btn>
              <div
                @click="
                  descargarArchivos(archivo.archivo, archivo.nombreArchivo)
                "
                class=""
              >
                <v-icon size="100px"> mdi-file-chart </v-icon>
                <br />
                {{ archivo.nombreArchivo }}
                <br />
                <h6>Fecha subida: {{ archivo.creado }}</h6>
                <h6>Subido por: {{ archivo.subidoPor }}</h6>
                <h6 v-if="archivo.comentarios!=''">Observaciones: {{ archivo.comentarios }}</h6>
                <h6 v-if="archivo.observaciones!=''">Archivo Requerido: {{ archivo.observaciones }}</h6>
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </FullBoxVue>

    <FullBoxVue>
      <h2>Observaciones</h2>

      <div
        class="chat-bubble"
        v-for="(object, index) in queryResultObservacionesAnalistaProveedor"
        :key="index"
      >
        <p class="message">{{ object.contenido }}</p>
        <span class="date">{{ object.subido }}</span>
        <button
          class="delete-btn"
          v-if="object.subidoPor == $dataUser[0].id || $dataUser[0].idRol == 3"
          @click="eliminarObservacion(object.id)"
        >
          Eliminar
        </button>
      </div>
    </FullBoxVue>

    <MiddleBox class="shadowHover">
      <v-form>
        <v-container>
          <v-row>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="infoSolicitud.id"
                label="Id"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="infoSolicitud.CentroCostos"
                label="Centro de costos"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                :value="infoSolicitud.NOMBRE_SERVICIO"
                label="Nombre servicio"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                :value="infoSolicitud.NOMBRE_SUBSERVICIO"
                label="Nombre subservicio"
                readonly
                filled
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="12">
              <v-text-field
                :value="infoSolicitud.NOMBRE_CIUDAD"
                label="Nombre ciudad"
                readonly
                filled
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="12">
              <v-text-field
                :value="infoSolicitud.NOMBRE_DEPARTAMENTO"
                label="Nombre departamento"
                readonly
                filled
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="12">
              <v-text-field
                :value="infoSolicitud.nombreCliente"
                label="Nombre cliente"
                readonly
                filled
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="12">
              <v-text-field
                :value="infoSolicitud.nombreEmpresa"
                label="Nombre empresa"
                readonly
                filled
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="12">
              <v-text-field
                :value="infoSolicitud.estado"
                label="Estado de la solicitud"
                readonly
                filled
              ></v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </MiddleBox>

    <MiddleBox class="shadowHover">
      <h2>Candidato(s)</h2>
      <v-form
        v-for="(candidato, index) in infoSolicitudCandidatos"
        :key="index"
      >
        <v-container>
          <v-row>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="candidato.nombreCandidato"
                label="Nombre candidato"
                readonly
                filled
              ></v-text-field>
            </v-col>

            <v-col cols="12" sm="12">
              <v-text-field
                v-model="candidato.tipo_doc"
                label="Tipo documento"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="candidato.num_doc"
                label="Número de documento"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="candidato.numTelefonico"
                label="Número telefónico"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="candidato.correo"
                label="Correo"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="candidato.direccion"
                label="Dirección"
                readonly
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12">
              <v-text-field
                v-model="candidato.observaciones"
                label="Observaciones"
                readonly
                filled
              ></v-text-field>
            </v-col>

            <v-col
              cols="12"
              sm="12"
              v-if="candidato.archivo != '' && candidato.archivo != null"
            >
              <v-btn
                color="success"
                @click="
                  descargarArchivos(candidato.archivo, candidato.nombreArchivo)
                "
                >Descargar archivo adjunto</v-btn
              >
            </v-col>
            <v-col cols="12" sm="12" v-else>
              Este candidato no cuenta con archivos
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </MiddleBox>
  </div>
</template>

<script>
import FullBoxVue from "../components/static/FullBox.vue";
import MiddleBox from "@/components/static/MiddleBox.vue";
import searchUserMultipleVue from "../components/searchUserMultiple.vue";

import {
  getPeticion,
  getCandidatos,
  downloadFile,
  getArchivos,
  subirArchivos,
  eliminarArchivos,
  getFunProv,
  updateEncargadoSolicitud,
  getEstadosPeticiones,
  updateEstadoSolicitud,
  updateObservacionesAnalistaProveedor,
  getObservacionesAnalista,
  eliminarObservacion,
} from "@/api";

export default {
  data() {
    return {
      infoSolicitud: {},
      infoSolicitudCandidatos: {},
      componentKey: 1,
      estadoSolicitud: null,
      estados: null,
      idSolicitud: this.$route.params.id,
      archivosSubidos: [],
      Images: null,
      valido: false,
      userSearchSelected: [],
      userSearch: [],
      observacionesAnalistaProveedor: "",
      queryResultObservacionesAnalistaProveedor: [],
    };
  },

  components: {
    FullBoxVue,
    MiddleBox,
    searchUserMultipleVue,
  },

  methods: {
    actualizarEncargado() {
      let bodySol = {
        idEncargado: this.userSearchSelected,
        idSolicitud: this.idSolicitud,
      };
      updateEncargadoSolicitud(bodySol).then(
        function (response) {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer);
              toast.addEventListener("mouseleave", this.$swal.resumeTimer);
            },
          });
          Toast.fire({
            icon: "success",
            title: "Solicitud enviada exitosamente",
          });
        }.bind(this)
      );
    },

    queryObservacionesAnalistaProveedor() {
      getObservacionesAnalista(this.idSolicitud).then(
        function (response) {
          this.queryResultObservacionesAnalistaProveedor = response.data;
        }.bind(this)
      );
    },

    eliminarObservacion(id) {
      eliminarObservacion(id).then(
        function (response) {
          this.queryObservacionesAnalistaProveedor();
        }.bind(this)
      );
    },

    actualizarObservacionesAnalistaProveedor() {
      let bodySol = {
        observacionesAnalistaProveedor: this.observacionesAnalistaProveedor,
        idSolicitud: this.idSolicitud,
      };
      updateObservacionesAnalistaProveedor(bodySol).then(
        function (response) {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer);
              toast.addEventListener("mouseleave", this.$swal.resumeTimer);
            },
          });
          Toast.fire({
            icon: "success",
            title: "Solicitud enviada exitosamente",
          });
          this.observacionesAnalistaProveedor = "";
          this.queryObservacionesAnalistaProveedor();
        }.bind(this)
      );
    },

    actualizarEstadoSolicitud() {
      let bodySol = {
        idEstado: this.estadoSolicitud,
        idSolicitud: this.idSolicitud,
      };
      updateEstadoSolicitud(bodySol).then(
        function (response) {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer);
              toast.addEventListener("mouseleave", this.$swal.resumeTimer);
            },
          });
          Toast.fire({
            icon: "success",
            title: "Solicitud enviada exitosamente",
          });
        }.bind(this)
      );
    },

    userSearchSelectedFunc(values) {
      this.userSearchSelected = values;
    },

    uploadFile() {
      this.Images = this.$refs.file.files[0];
      console.log(this.Images)
    },

    eliminarArchivo(id) {
      eliminarArchivos(id).then(
        function (response) {
          console.log(response.data);
          this.realizarConsultaArchivos();
        }.bind(this)
      );
    },

    realizarConsulta() {
      getPeticion(this.idSolicitud).then(
        function (response) {
          this.infoSolicitud = response.data;
          this.observacionesAnalistaProveedor = response.data.observaciones;
          this.estadoSolicitud = this.infoSolicitud.idEstado;
          this.componentKey += 1;
          getFunProv(this.infoSolicitud.ID_SERVICIO).then(
            function (response) {
              let userSearch = [];
              for (let user in response.data) {
                userSearch.push({
                  id: response.data[user].id,
                  nombre:
                    response.data[user].nombre +
                    " - " +
                    response.data[user].solicitudesAsignadas +
                    " solicitudes asignadas",
                });
                console.log(response.data[user]);
              }
              this.userSearch = userSearch;
            }.bind(this)
          );
        }.bind(this)
      );
    },

    realizarConsultaEstados() {
      getEstadosPeticiones().then(
        function (response) {
          this.estados = response.data;
        }.bind(this)
      );
    },

    realizarConsultaCandidatos() {
      getCandidatos(this.idSolicitud).then(
        function (response) {
          this.infoSolicitudCandidatos = response.data;
          this.componentKey += 1;
        }.bind(this)
      );
    },

    realizarConsultaArchivos() {
      getArchivos(this.idSolicitud).then(
        function (response) {
          this.archivosSubidos = response.data;
        }.bind(this)
      );
    },

    subirArchivo() {
      const formData = new FormData();
      formData.append("file", this.Images);
      formData.append("idSolicitud", this.idSolicitud);

      subirArchivos(formData).then(
        function (response) {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer);
              toast.addEventListener("mouseleave", this.$swal.resumeTimer);
            },
          });
          Toast.fire({
            icon: "success",
            title: "Solicitud enviada exitosamente",
          });
          this.realizarConsultaArchivos();
        }.bind(this)
      );
    },

    descargarArchivos(filepath, fileName) {
      downloadFile(filepath).then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", fileName);
        document.body.appendChild(link);
        link.click();
      });
    },
  },

  mounted() {
    this.realizarConsulta();

    this.queryObservacionesAnalistaProveedor();
  },

  watch: {
    infoSolicitud(newState) {
      if (newState != {}) {
        this.valido = true;
        this.realizarConsultaCandidatos();
        this.realizarConsultaArchivos();
        this.realizarConsultaEstados();
      }
    },
  },
};
</script>

<style>
.archivo {
  min-width: 300px;
  max-width: 300px;
  min-height: 170px;
  padding: 10px;
}

.archivo:hover {
  background: #385da6;
  color: #fff;
}

.archivo:hover > div > i {
  color: #fff;
}
.archivo:hover > div {
  cursor: pointer;
}

.archivo:hover > div > div > div label{
  color: #fff;
}

.archivo:hover > div > div > div > textarea{
  color: #fff;
}

.chat-bubble {
  background-color: #f1f0f0;
  border-radius: 15px;
  padding: 10px;
  width: 80%;
  display: inline-block;
  margin: 10px 0;
}

.message {
  margin: 0;
  font-size: 16px;
  line-height: 1.2;
}

.date {
  font-size: 12px;
  color: #666;
  display: block;
  margin-top: 5px;
}

.delete-btn {
  background-color: #f44336;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  margin-top: 10px;
  font-size: 14px;
  cursor: pointer;
}
</style>
