<template>
  <div v-if="$dataUser[0].rol == 'Cliente' || $dataUser[0].rol == 'Admin'">
    <FullBoxVue class="shadowHover">
      <h1>Nueva solicitud</h1>
      Los campos con un * son obligatorios
    </FullBoxVue>

    <FullBoxVue class="shadowHover" v-if="$dataUser[0].rol == 'Admin'">
      <h2>Seleccionar usuario</h2>
      <searchUserVue
        v-bind:people="userSearch"
        @selected="userSearchSelectedFunc"
      ></searchUserVue>
    </FullBoxVue>
    <MiddleBox class="shadowHover">
      <v-text-field
        v-model="CentroCostos"
        label="Centro de costos"
      ></v-text-field>
      <div class="formContent">
        <div v-for="(data, index) in schemaServicio" :key="index">
          Servicio: {{ index + 1 }}

          <v-select
            @change="buscarSubservicio(valuesServicio[index].servicio, index)"
            v-model="valuesServicio[index].servicio"
            :items="data.servicio"
            item-text="nombre"
            item-value="id"
            label="Servicio*"
          ></v-select>
          <v-select
            v-model="valuesServicio[index].subservicio"
            :items="data.subservicio"
            item-text="nombre"
            item-value="id"
            label="SubServicio*"
          ></v-select>
        </div>

        <v-btn color="success" @click="agregarServicios()"
          >Agregar servicio</v-btn
        >

        <br /><br />
        <BlitzForm
          :schema="schemaUbicacion"
          v-model="valuesUbicacion"
          :key="componentKeyUbicacion"
          gridGap="0"
          labelPosition="left"
        />
      </div>
    </MiddleBox>
    <MiddleBox class="shadowHover">
      <h2>Datos del candidato</h2>
      <BlitzForm
        :schema="schemaCandidato"
        v-model="valuesCandidato"
        gridGap="0"
        labelPosition="left"
      />
      <input type="file" @change="uploadFile" ref="file" />
      <br /><br />
    </MiddleBox>
    <FullBoxVue class="shadowHover">


      <v-alert v-if="errorRequeridos != ''" title="Hay errores en su formulario" type="error"> <span v-html="errorRequeridos"></span></v-alert>
      <div v-if="!loading">
        <v-btn color="primary"  @click="enviarFormulario" :disabled="errorRequeridos != ''">
          Enviar Solicitud
        </v-btn>
      </div>
      <div v-else>
        <loadingCSS>
          Se están creando las solicitudes... <br />
          Esto puede tomar unos momentos
        </loadingCSS>
      </div>
    </FullBoxVue>
  </div>
</template>

<script>
import FullBoxVue from "../components/static/FullBox.vue";
import { BlitzForm } from "blitzar";
import Vue from "vue";
import loadingCSS from "../components/static/loadingCSS.vue";

import {
  VTextField,
  VRadioGroup,
  VRadio,
  VSlider,
  VCheckbox,
  VSwitch,
  VSelect,
} from "vuetify/lib";
// All components that are used in the form need to be globally registered.
Vue.component("VTextField", VTextField);
Vue.component("VRadioGroup", VRadioGroup);
Vue.component("VRadio", VRadio);
Vue.component("VSlider", VSlider);
Vue.component("VCheckbox", VCheckbox);
Vue.component("VSwitch", VSwitch);
Vue.component("VSelect", VSelect);

import {
  getServicio,
  getSubServicio,
  getServicioEspecifico,
  getServicioEspecificoEspecifico,
  getDepartamento,
  getCiudad,
  getDocumento,
  newForm,
  getClientes,
} from "@/api";
import MiddleBox from "@/components/static/MiddleBox.vue";
import searchUserVue from "../components/searchUser.vue";

const valuesServiciodef = {
  servicio: "",
  subservicio: "",
};

const valuesUbicaciondef = {
  departamento: "",
  ciudad: "",
};

const valuesCandidatodef = {
  nombreCandidato: "",
  tipodocumento: "",
  documento: "",
  numeroCandidato: "",
  correo: "",
  direccion: "",
  observaciones: "",
};

export default {
  data() {
    return {
      valuesServicio: [],
      CentroCostos: "",
      schemaServicio: [],

      valuesUbicacion: {
        departamento: "",
        ciudad: "",
      },
      schemaUbicacion: [],
      componentKey: 0,
      componentKeyUbicacion: -1,

      valuesCandidato: {
        nombreCandidato: "",
        tipodocumento: "",
        documento: "",
        numeroCandidato: "",
        correo: "",
        direccion: "",
        observaciones: "",
      },
      schemaCandidato: [],
      Images: null,
      datosUser: {},
      userSearch: [],
      userSearchSelected: [],
      serviciosDefault: [],
      loading: false,
      errorRequeridos: "",
    };
  },

  methods: {
    userSearchSelectedFunc(values) {
      this.userSearchSelected = values;
    },
    recargarFormulario() {
      this.componentKey += 1;
      this.componentKeyUbicacion -= 1;
    },

    validarForm(schema, values) {
      let returnValue = true;
      for (let i = 0; i < schema.length; i++) {
        let element = schema[i];
        if (element.isRequired) {
          if (
            values[element.id] == "" ||
            values[element.id] === null ||
            values[element.id].length === 0
          ) {
            if (this.errorRequeridos == "") {
              this.errorRequeridos =
                " El campo " + element.label + " es requerido <br>";
            } else {
              this.errorRequeridos +=
                " El campo " + element.label + " es requerido <br>";
            }
            returnValue = false;
          }
        }
      }
      return returnValue;
    },

    validarServicios() {
      let values = this.valuesServicio;
      let returnValue = true;
      for (let i = 0; i < values.length; i++) {
        if (
          values[i].servicio == "" ||
          values[i].servicio === null ||
          values[i].servicio.length === 0 ||
          values[i].subservicio == "" ||
          values[i].subservicio === null ||
          values[i].subservicio.length === 0
        ) {
          if (this.errorRequeridos == "") {
            this.errorRequeridos =
              " Campos del servicio " + (i + 1) + " están incompletos <br>";
          } else {
            this.errorRequeridos +=
              " Campos del servicio " + (i + 1) + " están incompletos <br>";
          }
          returnValue = false;
        }
      }
      return returnValue;
    },

    validarFormularios() {
      this.errorRequeridos = "";
      console.log("Servicio: ");
      console.log(this.validarServicios());
      console.log("Ubicación: ");
      console.log(this.validarForm(this.schemaUbicacion, this.valuesUbicacion));
      console.log("Candidato: ");
      console.log(this.validarForm(this.schemaCandidato, this.valuesCandidato));
    },

    uploadFile() {
      this.Images = this.$refs.file.files[0];
    },

    enviarFormulario() {
      this.loading = true;
      const formData = new FormData();
      formData.append("file", this.Images);
      formData.append("CentroCostos", this.CentroCostos);
      formData.append("InfoServicio", JSON.stringify(this.valuesServicio));
      formData.append("Ubicacion", JSON.stringify(this.valuesUbicacion));
      formData.append("Candidato", JSON.stringify(this.valuesUbicacion));
      if (this.$dataUser[0].rol == "Admin") {
        formData.append("Usuario", JSON.stringify(this.userSearchSelected));
      }
      newForm(formData).then(
        function (response) {
          console.log(response.data);
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer);
              toast.addEventListener("mouseleave", this.$swal.resumeTimer);
            },
          });
          Toast.fire({
            icon: "success",
            title: "Solicitud enviada exitosamente",
          });
          //this.valuesServicio = valuesServiciodef;
          //this.valuesUbicacion = valuesUbicaciondef;
          //this.valuesCandidato = valuesCandidatodef;
          this.loading = false;
          this.recargarFormulario();
          //setTimeout('document.location.reload()',1000);
        }.bind(this)
      );
      console.log(formData);
    },

    agregarServicios() {
      this.schemaServicio.push(
        structuredClone({
          servicio: this.serviciosDefault,
          subservicio: [],
        })
      );
      this.valuesServicio.push(structuredClone(valuesServiciodef));
    },

    consultaServicio(indiceMayor) {
      getServicio().then(
        function (response) {
          this.preServicios = response.data;
          console.log(this.preServicios);
          this.serviciosDefault = [];
          for (let i = 0; i < this.preServicios.length; i++) {
            console.log(this.preServicios[i]);
            this.serviciosDefault.push({
              id: this.preServicios[i].ID_SERVICIO,
              nombre: this.preServicios[i].NOMBRE_SERVICIO,
            });
          }

          this.agregarServicios();
          this.recargarFormulario();
        }.bind(this)
      );
    },

    buscarSubservicio(id, indice) {
      this.valuesServicio[indice].subservicio = ""
      getSubServicio(id).then(
        function (response) {
          if (response.data.length > 0) {
            let preSubServicios = response.data;
            let subserviciosDefault = [];
            for (let i = 0; i < preSubServicios.length; i++) {
              subserviciosDefault.push({
                id: preSubServicios[i].ID_SUBSERVICIO,
                nombre: preSubServicios[i].NOMBRE_SUBSERVICIO,
              });
            }
            this.schemaServicio[indice].subservicio = subserviciosDefault;
            this.recargarFormulario();
          }
        }.bind(this)
      );
    },

    consultaServicioEspecifico(id) {
      getServicioEspecifico(id).then(
        function (response) {
          const indice = 3;
          if (response.data.length > 0) {
            const itemsOptionsTemplate = {
              value: "",
              text: "Seleccione el Servicio Específico",
              disabled: true,
            };

            const options = [itemsOptionsTemplate];

            for (let i = 0; i < response.data.length; i++) {
              // Solución planteada así por un error de referencia de memoria
              const itemsOptionsTemplate = {
                value: "",
                text: "",
              };
              const subTemplate = itemsOptionsTemplate;
              itemsOptionsTemplate.value =
                response.data[i].ID_SERVICIO_ESPECIFICO;
              subTemplate.text = response.data[i].NOMBRE_SERVICIO_ESPECIFICO;
              options.push(subTemplate);
            }

            if (this.schemaServicio[indice] == null) {
              this.schemaServicio.splice(indice, 0, {
                id: "servicioEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico*",
                items: options,
                isRequired: true,
              });
            } else {
              this.schemaServicio[indice] = {
                id: "servicioEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico*",
                items: options,
                isRequired: true,
              };

              this.recargarFormulario();
            }
          } else {
            this.schemaServicio.splice(indice, 1);
          }
        }.bind(this)
      );
    },
    consultaServicioEspecificoEspecifico(id) {
      getServicioEspecificoEspecifico(id).then(
        function (response) {
          const indice = 4;
          console.log(response.data);
          if (response.data.length > 0) {
            const itemsOptionsTemplate = {
              value: "",
              text: "Seleccione el Servicio Específico",
              disabled: true,
            };

            const options = [itemsOptionsTemplate];

            for (let i = 0; i < response.data.length; i++) {
              // Solución planteada así por un error de referencia de memoria
              const itemsOptionsTemplate = {
                value: "",
                text: "",
              };
              const subTemplate = itemsOptionsTemplate;
              itemsOptionsTemplate.value =
                response.data[i].ID_SERVICIO_ESPECIFICO2;
              subTemplate.text = response.data[i].NOMBRE_SERVICIO_ESPECIFICO2;
              options.push(subTemplate);
            }

            if (this.schemaServicio[indice] == null) {
              this.schemaServicio.splice(indice, 0, {
                id: "servicioEspecificoEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico*",
                items: options,
                isRequired: true,
              });
            } else {
              this.schemaServicio[indice] = {
                id: "servicioEspecificoEspecifico",
                span: true,
                component: "VSelect",
                label: "Servicio Específico*",
                items: options,
                isRequired: true,
              };

              this.recargarFormulario();
            }
          } else {
            this.schemaServicio.splice(indice, 1);
          }
        }.bind(this)
      );
    },

    consultaCiudad(id) {
      getCiudad(id).then(
        function (response) {
          const indice = 1;
          if (response.data.length > 0) {
            const itemsOptionsTemplate = {
              value: "",
              text: "Seleccione la ciudad",
              disabled: true,
            };

            const options = [itemsOptionsTemplate];

            for (let i = 0; i < response.data.length; i++) {
              // Solución planteada así por un error de referencia de memoria
              const itemsOptionsTemplate = {
                value: "",
                text: "",
              };
              const subTemplate = itemsOptionsTemplate;
              itemsOptionsTemplate.value = response.data[i].ID_CIUDAD;
              subTemplate.text = response.data[i].NOMBRE_CIUDAD;
              options.push(subTemplate);
            }

            if (this.schemaUbicacion[indice] == null) {
              this.schemaUbicacion.splice(indice, 0, {
                id: "ciudad",
                span: true,
                component: "VSelect",
                label: "Ciudad*",
                items: options,
                isRequired: true,
              });
            } else {
              this.schemaUbicacion[indice] = {
                id: "ciudad",
                span: true,
                component: "VSelect",
                label: "Ciudad*",
                items: options,
                isRequired: true,
              };
            }
          } else {
            this.schemaUbicacion.splice(indice, 1);
          }
          this.recargarFormulario();
          this.validarFormularios();
        }.bind(this)
      );
    },
  },

  mounted() {
    getClientes().then(
      function (response) {
        this.userSearch = response.data;
      }.bind(this)
    );

    this.consultaServicio(0);

    getDepartamento().then(
      function (response) {
        const indice = 0;
        const totalForms = 5;
        const itemsOptionsTemplate = {
          value: "",
          text: "Seleccione el departamento",
          disabled: true,
        };

        const options = [itemsOptionsTemplate];

        for (let i = 0; i < response.data.length; i++) {
          // Solución planteada así por un error de referencia de memoria
          const itemsOptionsTemplate = {
            value: "",
            text: "",
          };
          const subTemplate = itemsOptionsTemplate;
          itemsOptionsTemplate.value = response.data[i].ID_DEPARTAMENTO;
          subTemplate.text = response.data[i].NOMBRE_DEPARTAMENTO;
          options.push(subTemplate);
        }

        if (this.schemaUbicacion[indice] == null) {
          this.schemaUbicacion.splice(indice, 0, {
            id: "departamento",
            span: true,
            component: "VSelect",
            label: "Departamento*",
            items: options,
            isRequired: true,
          });
        } else {
          this.schemaUbicacion[indice] = {
            id: "departamento",
            span: true,
            component: "VSelect",
            label: "Departamento*",
            items: options,
            isRequired: true,
          };
        }
        this.recargarFormulario();
      }.bind(this)
    );

    getDocumento().then(
      function (response) {
        console.log(response.data);
        const indice = 0;
        const totalForms = 5;
        const itemsOptionsTemplate = {
          value: "",
          text: "Seleccionar tipo de documento",
          disabled: true,
        };

        const options = [itemsOptionsTemplate];

        for (let i = 0; i < response.data.length; i++) {
          // Solución planteada así por un error de referencia de memoria
          const itemsOptionsTemplate = {
            value: "",
            text: "",
          };
          const subTemplate = itemsOptionsTemplate;
          itemsOptionsTemplate.value = response.data[i].ID_TIPO_DOCUMENTO;
          subTemplate.text = response.data[i].DESC_COMPLE_TIPO_DOCUMENTOcol;
          options.push(subTemplate);
        }

        const opcionesDocumentos = {
          id: "tipodocumento",
          span: true,
          component: "VSelect",
          label: "Tipo de documento*",
          items: options,
          isRequired: true,
        };

        this.schemaCandidato = [
          {
            id: "nombreCandidato",
            component: "VTextField",
            label: "Nombre del candidato*",
            isRequired: true,
          },
          opcionesDocumentos,
          {
            id: "documento",
            component: "VTextField",
            label: "Número de documento*",
            isRequired: true,
          },
          {
            id: "numeroCandidato",
            component: "VTextField",
            label: "Número telefónico*",
            isRequired: true,
          },
          {
            id: "correo",
            component: "VTextField",
            label: "Correo*",
            isRequired: true,
          },
          {
            id: "direccion",
            component: "VTextField",
            label: "Dirección*",
            isRequired: true,
          },
          {
            id: "observaciones",
            component: "VTextField",
            label: "Observaciones",
          },
        ];

        this.recargarFormulario();
      }.bind(this)
    );
  },

  watch: {
    valuesUbicacion(newValue, oldValue) {
      if (
        newValue.departamento &&
        newValue.departamento != oldValue.departamento
      ) {
        this.valuesUbicacion.ciudad = "";
        this.consultaCiudad(newValue.departamento);
      }
      this.validarFormularios();
    },

    valuesServicio: {
      handler(val){
        this.validarFormularios();
     },
     deep: true

    },
    valuesCandidato(newValue, oldValue) {
      this.validarFormularios();
    },
  },

  components: {
    FullBoxVue,
    BlitzForm,
    MiddleBox,
    searchUserVue,
    loadingCSS,
  },
};
</script>

<style scoped>
div {
  width: 100%;
}

VTextField {
  border: 2px solid #8c939d !important;
}

VSelect {
  border: 2px solid #8c939d !important;
  border-radius: 10px;
  padding: 2px;
}
</style>
