<template>
  <FullBoxVue class="shadowHover">
    <div class="formContent">
      <h1>Registrar Cliente</h1>
      <br />
      <hr />
      <br /><br />
      <BlitzForm
        :schema="schemaRegister"
        v-model="valuesRegister"
        :key="componentKey"
        gridGap="0"
        labelPosition="left"
      />

      <div v-for="(servicio, index) in servicios" :key="index">
        {{ servicio.NOMBRE_SERVICIO }}

        <v-text-field
          @change="updateCosto($event, index)"
          :label="'Costo de servicio ' + servicio.NOMBRE_SERVICIO"
          type="number"
        ></v-text-field>
      </div>

      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="valuesRegister.fechaCorte"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-combobox
            v-model="valuesRegister.fechaCorte"
            chips
            small-chips
            label="Fecha de corte"
            prepend-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
          ></v-combobox>
        </template>
        <v-date-picker v-model="valuesRegister.fechaCorte" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu = false"> Cancelar </v-btn>
          <v-btn
            text
            color="primary"
            @click="$refs.menu.save(valuesRegister.fechaCorte)"
          >
            OK
          </v-btn>
        </v-date-picker>
      </v-menu>
      <v-btn color="primary" dark @click="enviarFormulario"> Enviar </v-btn>
    </div>
  </FullBoxVue>
</template>

<script>
import FullBoxVue from "../components/static/FullBox.vue";
import { BlitzForm } from "blitzar";
import searchUserMultipleVue from "../components/searchUserMultiple.vue";
import Vue from "vue";

import {
  VTextField,
  VRadioGroup,
  VRadio,
  VSlider,
  VCheckbox,
  VSwitch,
  VSelect,
} from "vuetify/lib";
// All components that are used in the form need to be globally registered.
Vue.component("VTextField", VTextField);
Vue.component("VRadioGroup", VRadioGroup);
Vue.component("VRadio", VRadio);
Vue.component("VSlider", VSlider);
Vue.component("VCheckbox", VCheckbox);
Vue.component("VSwitch", VSwitch);
Vue.component("VSelect", VSelect);

import {
  getDocumento,
  postUser,
  getRoles,
  getServicio,
  getClientes,
} from "@/api";

const valuesRegisterdef = {
  nombre: "",
  tipoDocumento: "",
  contacto: "",
  correo: "",
  documento: "",
  rol: 2,
  serviciosAsociados: "",
  fechaCorte: "",
  costos: []
};
export default {
  data() {
    return {
      valuesRegister: {
        nombre: "",
        tipoDocumento: "",
        contacto: "",
        correo: "",
        documento: "",
        rol: 2,
        serviciosAsociados: "",
        fechaCorte: "",
        userSearchSelected: [],
        costos: []
      },
      menu: false,
      serviciosDefault: [],
      schemaRegister: [
        {
          id: "nombre",
          component: "VTextField",
          label: "Razón Social",
        },
        //Id Tipo Documento
        {},
        {
          id: "documento",
          component: "VTextField",
          label: "No. Cedula / NIT",
        },
        {
          id: "contacto",
          component: "VTextField",
          label: "Teléfono",
        },
        {
          id: "correo",
          component: "VTextField",
          label: "Correo",
        },
        //Info contacto
        {
          id: "empresa",
          component: "VTextField",
          label: "Nombre empresa",
        },
        {
          id: "representante_legal",
          component: "VTextField",
          label: "Representante Legal",
        },
        {
          id: "direccion",
          component: "VTextField",
          label: "Dirección de empresa",
        },
      ],
      componentKey: 0,
      costo: 0,
      userSearch: [],
      servicios: [],
      costosServicios: [],
    };
  },

  methods: {
    userSearchSelectedFunc(values) {
      this.valuesRegister.userSearchSelected = values;
    },
    recargarFormulario() {
      this.componentKey -= 1;
    },

    updateCosto(value, index){
      this.costosServicios[index].costo = Number(value)
      this.valuesRegister.costos =  JSON.parse(JSON.stringify(this.costosServicios))

    },
    enviarFormulario() {
      postUser(this.valuesRegister)
        .then(
          function (response) {
            console.log(response);
            const Toast = this.$swal.mixin({
              toast: true,
              position: "top-end",
              showConfirmButton: false,
              timer: 3000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.addEventListener("mouseenter", this.$swal.stopTimer);
                toast.addEventListener("mouseleave", this.$swal.resumeTimer);
              },
            });
            if (response.data == "") {
              Toast.fire({
                icon: "success",
                title: "Usuario registrado exitosamente",
              });
              this.valuesRegister = valuesRegisterdef;
              this.recargarFormulario();
            } else {
              Toast.fire({
                icon: "error",
                title:
                  "Error al registrar el usuario, correo no válido y/o ya usado.",
              });
            }
          }.bind(this)
        )
        .catch(function (response) {
          const Toast = this.$swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", this.$swal.stopTimer);
              toast.addEventListener("mouseleave", this.$swal.resumeTimer);
            },
          });
          Toast.fire({
            icon: "error",
            title:
              "Error al registrar el usuario, correo no válido y/o ya usado.",
          });
        });
    },

    consultaServicio() {
      getServicio().then(
        function (response) {
          this.servicios = response.data;

          this.costosServicios = [];
          for (let i = 0; i < this.servicios.length; i++) {
            this.costosServicios[i] = {
              idServicio: this.servicios[i].ID_SERVICIO,
              costo: 0,
            };
          }
          this.valuesRegister.costos = JSON.parse(JSON.stringify(this.costosServicios))
        }.bind(this)
      );
    },
  },

  mounted() {
    getClientes().then(
      function (response) {
        this.userSearch = response.data;
      }.bind(this)
    );

    getDocumento().then(
      function (response) {
        const indice = 1;
        const itemsOptionsTemplate = {
          value: "",
          text: "Seleccionar tipo de documento",
          disabled: true,
        };

        const options = [itemsOptionsTemplate];

        for (let i = 0; i < response.data.length; i++) {
          // Solución planteada así por un error de referencia de memoria
          const itemsOptionsTemplate = {
            value: "",
            text: "",
          };
          const subTemplate = itemsOptionsTemplate;
          itemsOptionsTemplate.value = response.data[i].ID_TIPO_DOCUMENTO;
          subTemplate.text = response.data[i].DESC_TIPO_DOCUMENTO;
          options.push(subTemplate);
        }

        this.schemaRegister[indice] = {
          id: "tipoDocumento",
          span: true,
          component: "VSelect",
          label: "Tipo de documento",
          items: options,
        };

        this.recargarFormulario();
      }.bind(this)
    );
    this.consultaServicio();
  },

  watch: {
    valuesRegister(newValue, oldValue) {
      this.valuesRegister.fechaCorte = oldValue.fechaCorte;
      this.valuesRegister.serviciosAsociados = oldValue.serviciosAsociados;
      this.valuesRegister.userSearchSelected = oldValue.userSearchSelected;
      this.valuesRegister.costos = oldValue.costos;
    }
  },

  components: {
    FullBoxVue,
    BlitzForm,
    searchUserMultipleVue,
  },
};
</script>

<style></style>
